.wp-block-vml-fixtures-admin {
	.buttons-row {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		column-gap: 1em;
		button {
			flex: 1;
			&.activeBtn {
				color: #fff;
				background-color: #c36;
				text-decoration: none;

				outline: 5px auto -webkit-focus-ring-color;
			}
		}
	}
	.loading {
		width: 100%;
	}
	.admin-fixture {
		display: flex;
		gap: 1em;
		.face-data-display {
			flex: 2;
		}
		.panel-data-display {
			flex: 1;
		}
	}
	.shelf {
		display: flex;
		flex-wrap: wrap;
		border: 1px solid #ccc;
		align-items: center;
		justify-content: center;
		h3 {
			flex-basis: 100%;
			margin: 0 0 0 0.2em;
		}
		.group-position-8 {
			flex-basis: 100%;
		}
		.item-group {
			margin: 0 0.2em;

			.item {
				border: 1px solid #000;
				margin: 0.5em auto;
				background-size: cover;

				.smallp {
					font-size: 0.8em;
					margin: 0;
					line-height: 0.8em;
				}
			}
		}
	}
}
